/* FADE IN ANIMATION */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(25px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===============================
   PREMIUM HERO CAROUSEL (FINAL)
================================ */

.carousel {
  position: relative;
  height: 380px;
  border-radius: 24px;
  overflow: hidden;
  margin: 25px 0 45px;
  background: #000;
  box-shadow: 0 30px 70px rgba(0, 0, 0, 0.25);
}

.carouselSlide {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  opacity: 0;
  transform: scale(1.08);
  transition: opacity 1s ease, transform 1.3s ease;
}

.carouselSlide.active {
  opacity: 1;
  transform: scale(1);
}

/* Dark gradient overlay */
.carouselOverlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.65),
    rgba(0, 0, 0, 0.25),
    rgba(0, 0, 0, 0)
  );
}

/* Text Card */
.carouselText {
  position: absolute;
  left: 50px;
  bottom: 50px;
  max-width: 460px;
  padding: 30px;
  border-radius: 22px;
  background: rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(14px);
  color: #fff;
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.45);
  animation: slideUp 0.8s ease;
}

.carouselText h2 {
  font-size: 34px;
  font-weight: 800;
  margin-bottom: 8px;
}

.carouselText p {
  font-size: 17px;
  opacity: 0.95;
}

/* Tag */
.bannerTag {
  display: inline-block;
  background: linear-gradient(90deg, #ff6f00, #ff9100);
  padding: 6px 16px;
  border-radius: 30px;
  font-size: 13px;
  font-weight: 800;
  margin-bottom: 12px;
  box-shadow: 0 6px 18px rgba(255, 111, 0, 0.5);
}

/* CTA Button */
.bannerBtn {
  margin-top: 16px;
  padding: 14px 26px;
  border-radius: 14px;
  border: none;
  background: #fff;
  color: #111;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
}

.bannerBtn:hover {
  background: #ff9100;
  color: #fff;
  transform: translateY(-2px);
}

/* Arrows */
.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.85);
  font-size: 28px;
  cursor: pointer;
  display: grid;
  place-items: center;
  transition: 0.3s;
}

.arrow:hover {
  transform: translateY(-50%) scale(1.1);
}

.arrow.left {
  left: 20px;
}

.arrow.right {
  right: 20px;
}

/* Dots */
.dots {
  position: absolute;
  bottom: 18px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

.dot {
  width: 18px;
  height: 6px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.45);
  cursor: pointer;
  transition: 0.3s;
}

.dot.active {
  width: 36px;
  background: #fff;
}

/* Mobile */
@media (max-width: 768px) {
  .carousel {
    height: 220px;
    margin-top: 12px;
  }

  .carouselText {
    left: 16px;
    bottom: 16px;
    padding: 16px;
  }

  .carouselText h2 {
    font-size: 20px;
  }

  .carouselText p {
    font-size: 14px;
  }
}

/* Animation */
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
