/* ================= HEADER BAR ================= */
.headerContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 75px;
  padding: 10px 40px;

  background: linear-gradient(135deg, #2874f0, #003cb3);
  backdrop-filter: blur(10px);

  display: flex;
  align-items: center;
  justify-content: space-between;

  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.25);
  z-index: 999;

  animation: slideDown 0.6s ease;
}

/* HEADER OFFSET */
body {
  padding-top: 95px !important;
}

/* ================= LOGO ================= */
.logo {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.logo:hover {
  transform: scale(1.05);
}

.logo img {
  width: 52px;
  height: 52px;
  border-radius: 12px;
  object-fit: cover;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.35);
  transition: transform 0.4s ease;
}

.logo:hover img {
  transform: rotate(-2deg) scale(1.1);
}

.logoName {
  font-size: 22px;
  font-weight: 700;
  color: #fff;
  letter-spacing: 0.5px;
}

/* ================= SEARCH BOX ================= */
.searchBox {
  background: #ffffff;
  border-radius: 14px;
  padding: 10px 18px;
  display: flex;
  align-items: center;
  width: 420px;

  border: 1px solid #ddd;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05);

  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.searchBox:focus-within {
  box-shadow: 0 0 0 4px rgba(40, 116, 240, 0.25);
  transform: translateY(-1px);
}

.searchBox input {
  border: none;
  outline: none;
  width: 100%;
  margin-left: 10px;
  font-size: 15px;
}

.searchIcon {
  color: #2874f0;
  font-size: 18px;
}

/* ================= CATEGORY ================= */
.categoryMenu select {
  padding: 10px 16px;
  border-radius: 12px;
  border: none;
  font-size: 15px;
  background: #ffffff;
  cursor: pointer;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.categoryMenu select:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.2);
}

/* ================= NAV ================= */
.navActions {
  display: flex;
  align-items: center;
  gap: 28px;
}

.navItem {
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  position: relative;
  transition: opacity 0.3s ease;
}

.navItem::after {
  content: "";
  position: absolute;
  bottom: -6px;
  left: 0;
  width: 0;
  height: 2px;
  background: #ff9800;
  transition: width 0.3s ease;
}

.navItem:hover::after {
  width: 100%;
}

.navItem:hover {
  opacity: 0.85;
}

/* ================= CART ================= */
.cartLink {
  display: flex;
  align-items: center;
  color: #ffffff;
  position: relative;
  transition: transform 0.3s ease;
}

.cartLink:hover {
  transform: scale(1.1);
}

.cartCount {
  background: linear-gradient(135deg, #ff9800, #ff6f00);
  padding: 3px 8px;
  border-radius: 50px;
  font-size: 13px;
  font-weight: 700;
  color: #fff;

  position: absolute;
  top: -10px;
  right: -14px;

  animation: pulse 1.5s infinite;
}

/* ================= PROFILE ================= */
.profileWrapper {
  position: relative;
}

.profileBtn {
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  padding: 6px 12px;
  border-radius: 10px;
  transition: background 0.3s ease;
}

.profileBtn:hover {
  background: rgba(255, 255, 255, 0.15);
}

.profileName {
  font-weight: 500;
  font-size: 14px;
  color: #fff;
}

/* DROPDOWN */
.profileDropdown {
  position: absolute;
  right: 0;
  top: 48px;
  background: #ffffff;
  color: #333;
  width: 170px;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 1000;

  animation: dropdownFade 0.3s ease;
}

.profileDropdown div {
  padding: 12px 16px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.25s ease, padding-left 0.25s ease;
}

.profileDropdown div:hover {
  background: #f1f3f6;
  padding-left: 20px;
}

.logoutItem {
  color: #e63946;
  font-weight: 600;
}

/* ================= ANIMATIONS ================= */
@keyframes slideDown {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes dropdownFade {
  from {
    opacity: 0;
    transform: translateY(-10px) scale(0.95);
  }

  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 152, 0, 0.7);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(255, 152, 0, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(255, 152, 0, 0);
  }
}

/* ================= MOBILE ================= */
.mobileMenuBtn {
  display: none;
  color: white;
  cursor: pointer;
}

.desktopOnly {
  display: flex;
}

/* DRAWER */
.drawerOverlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 998;
}

.mobileDrawer {
  position: fixed;
  top: 0;
  left: 0;
  width: 280px;
  height: 100vh;
  background: linear-gradient(135deg, #2874f0, #003cb3);
  color: white;
  z-index: 999;
  padding: 20px;
}

.drawerHeader {
  display: flex;
  justify-content: space-between;
  font-weight: 600;
  margin-bottom: 20px;
}

.drawerSearch {
  background: white;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  gap: 8px;
  margin-bottom: 20px;
}

.drawerSearch input {
  border: none;
  outline: none;
  width: 100%;
}

.drawerSection h4 {
  margin: 12px 0;
  font-size: 14px;
  opacity: 0.9;
}

.drawerSection div {
  padding: 10px 0;
  cursor: pointer;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

/* ================= RESPONSIVE ================= */
@media (max-width: 900px) {
  .desktopOnly {
    display: none;
  }

  .mobileMenuBtn {
    display: block;
  }

  .headerContainer {
    padding: 10px 20px;
  }
}
